<template>
  <div>
    <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-weixin"></use>
    </svg>
    <button class="van-button" @click="doSomeThing">关闭</button>
  </div>
</template>
<script type="text/ecmascript-6">
  import * as localStoage from '@/utils/localStorage';
  
  export default {
    name: 'user',
    created(){
      
    },
    mounted(){
      


    },
    methods:{
      doSomeThing(){
        this.$LoadingMaskOpen({
          opacity:true
        })

        setTimeout(() => {
          this.$LoadingMaskClose()
        }, 3000);
      }
    }
    
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
    
</style>



